<template>
  <el-form label-width="120px" :model="budget" :rules="rules" :inline="true" ref="budgetForm">
    <el-form-item label="预算制定机构" prop="formulateInstitution">
      <el-select v-model="budget.formulateInstitution" placeholder="请选择预算制定机构" clearable>
        <el-option v-for="item in formData.companyList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算类型" prop="type">
      <el-select v-model="budget.type" placeholder="请选择预算类型" clearable>
        <el-option v-for="item in formData.types" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算渠道" prop="channel">
      <el-select v-model="budget.channel" placeholder="请选择预算渠道" clearable>
        <el-option v-for="item in formData.channelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算时间段" prop="timeSlot">
      <el-select v-model="budget.timeSlot" placeholder="请选择时间段" clearable>
        <el-option v-for="item in timeSlots" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算口径" prop="bore">
      <el-select v-model="budget.bore" placeholder="请选择口径" clearable>
        <el-option v-for="item in formData.boreList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="业务分类" prop="productType">
      <el-select v-model="budget.productType" placeholder="请选择业务分类" clearable>
        <el-option v-for="item in formData.productTypeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <slot></slot>
    <el-form-item label="版本" prop="version" v-if="versionShow">
      <el-input v-model="budget.version"></el-input>
    </el-form-item>
    <slot name="last"></slot>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'QueryForm',
  props: {
    budget: {
      type: Object,
      required: true
    },
    rules: {
      type: Object,
      default: null
    },
    versionShow: {
      type: Boolean,
      default: true
    },
    validMethod: {
      type: String
    }
  },
  data () {
    return {
      timeSlots: []
    }
  },
  created () {
    this.$store.dispatch('initFormData')
  },
  computed: {
    ...mapState({
      formData: state => state.budget.formData
    }),
    type () {
      return this.budget.type
    }
  },
  watch: {
    type: function (val) {
      this.budget.timeSlot = ''
      if (val !== '') {
        const obj = this.formData.types.find(item => item.code === val)
        this.timeSlots = obj.timeSlots
        if (obj.timeSlots.length > 0) {
          this.budget.timeSlot = obj.timeSlots[0].code
        }
      }
    }
  },
  methods: {
    valid () {
      this.$refs['budgetForm'].validate(valid => {
        if (valid) {
          this.$emit(this.validMethod)
        }
      })
    },
    reset () {
      this.timeSlots = []
      this.$refs['budgetForm'].resetFields()
    }
  }
}
</script>

<style scoped>

</style>



